export default {
	data() {
		return {
			swIndex: true,
			provinceArr: ['桂', '粤', '京', '津', '渝', '沪', '冀', '晋', '辽', '吉', '黑', '苏', '浙', '皖', '闽', '赣', '鲁', '豫', '鄂', '湘',
				'琼', '川', '贵', '云', '陕', '甘', '青', '蒙', '宁', '新', '藏', '使', '领', '警', '学', '港', '澳'
			],
			strArr: ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0', 'Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P', 'A',
				'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L', 'Z', 'X', 'C', 'V', 'B', 'N', 'M'
			],
			show: false,
			ani: ''
		}
	},
	props: {
		showKeyboard: {
			type: Boolean,
			default: false
		}
	},
	watch:{
		showKeyboard (newV) {
			console.log(newV)
			if (newV) {
				this.open()
			} else {
				this.close()
			}
		}
	},
	methods: {
		moveHandle(e) {
			// console.log('阻止蒙层下面元素滚动', e)
		},
		changeKeyBoard () {
			this.swIndex = !this.swIndex
		},
		choose(item) {
			// console.log('选择', item)
			this.$emit('onInput', item)
		},
		delHandle() {
			this.$emit('onDelete')
		},
		confirm() {
			this.$emit('onConfirm')
			this.close('mask')
		},
		open() {
			this.show = true
			setTimeout(() => {
				this.ani = 'top'
			}, 20)
		},
		close (type) {
			if (type == 'mask') {
				this.$emit('update:showKeyboard', false)
			}
			this.ani = ''
			setTimeout(() => {
				this.show = false
			}, 200)
		}
	},
	onShow() {
		console.log('parent root', this.$parent)
	}
}
